
.home-parallax {
  height: 360px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.home-parallax .home-parallax-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
}
.home-parallax .home-parallax-content h1 {
  margin: 0;
}
.introduction {
  color: #fff;
  font-size: 24px;
}
.water-fall {
  margin-top: 40px;
}
.water-fall:after {
  content: '';
  clear: both;
}
.water-fall-list {
  float: left;
}
.member {
  width: 100%;
  position: relative;
}
.member:hover .member-hover-layer {
  opacity: 1;
}
.member-hover-layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  background-color: rgba(0,0,0,0.6);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.member-photo {
  width: 100%;
  display: block;
}
.member-name {
  color: #fff;
  font-size: 18px;
  letter-spacing: 2px;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  height: 80px;
  -webkit-transition: height 0.4s ease;
  transition: height 0.4s ease;
  text-align: center;
  border-bottom: 1px solid #dcdfe6;
  background-color: #fff;
  z-index: 10;
}
.app-header.mini {
  height: 60px;
}
.navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
}
.navbar:after {
  content: ' ';
  clear: both;
}
.navbar-item {
  padding: 6px 15px;
}
.navbar-item.active {
  color: #1989fa;
}
#app {
  width: 100%;
  padding-top: 60px;
}